@import url('https://fonts.googleapis.com/css2?family=Orbitron&display=swap');

.App {
  text-align: center;
  background-color: lavender;
  color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-image: url('/src/images/background.webp');
  background-size: cover;
  background-position: center center; /* Ensures the background is centered */
  background-repeat: no-repeat; /* Prevents the background from repeating */
  animation: fadeIn 2s ease-out;
}

.App-header {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cli-button {
  background-color: transparent;
  border: none;
  color: rgb(170, 167, 167);
  font-family: monospace;
  font-size: 20px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.cli-button:hover {
  transform: scale(1.05);
}

.cli-prompt {
  color: #be0e0e;
  margin-right: 20px;
  font-weight: bold;
}

.title {
  font-family: 'Orbitron', sans-serif;
  color: #f10707;
  font-size: 3rem;
  margin-bottom: 10px;
}

.subtitle {
  font-family: 'Orbitron', sans-serif;
  font-size: 1.5rem;
  margin-bottom: 20px;
  color: #f10707;
  text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073, 0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073;
}

.services-menu {
  list-style: none;
  padding: 0;
  margin-top: 20px;
  text-align: center;
  width: 100%;
}

.services-menu li {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
  padding: 10px;
  cursor: pointer;
  position: relative;
  color: white;
}

.App-header.visible .services-menu li {
  opacity: 1;
  transform: translateY(0);
}

/* Responsive design adjustments */
@media (max-width: 768px) {
  .App {
    font-size: 15px; /* Smaller font size for mobile */
  }

  .title {
    font-size: 2rem; /* Smaller title size for mobile */
  }

  .subtitle {
    font-size: 1rem; /* Smaller subtitle size for mobile */
  }

  .cli-button {
    font-size: 16px; /* Smaller button text on mobile */
  }

  .services-menu li {
    font-size: 0.9rem; /* Smaller text in services menu on mobile */
  }

  .logo-container img {
    max-height: 100px; /* Smaller logo on mobile */
  }
}
/* Add this to your App.css */
.title {
  font-family: 'Orbitron', sans-serif;
  font-size: 3rem;
  color: #f10707; /* Red color */
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3); /* Slight text shadow for depth */
  background-image: linear-gradient(45deg, #f10707, #c10505); /* Red gradient for depth */
  -webkit-background-clip: text; /* Clip text to background */
  -webkit-text-fill-color: transparent; /* Hide text fill */
  margin-bottom: 10px; /* Adjust spacing */
}
/* Add this to your App.css */
.logo-container img {
  max-height: 140px; /* Adjust the max height as needed */
  filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.5)); /* Shadow for depth */
}
